.adress {
  cursor: pointer;
  &__city-wrapper {
    font-size: 0;
    margin-bottom: 10px;
    .icon {
      vertical-align: middle;
      display: inline-block;
      margin-bottom: 2px;
      margin-right: 12px;
      color: $darkBlue;
      font-size: 21px; } }
  &__city {
    display: inline-block;
    vertical-align: middle;
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: -0.006em;
    font-size: 21px; }
  &__phone,
  &__street {
    font-family: $gilroy;
    font-weight: 300;
    line-height: 1; }
  a {
    border-bottom: 1px solid transparent;
    transition: border-color .3s;
    @include hover {
      border-color: $white; } } }

.connection {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  &.text-right {
    justify-content: flex-end; }
  &__text {
    min-width: 208px;
    font-family: $gilroy;
    font-size: 21px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: -0.005em;
    color: $white;
    .connection_modal & {
      color: $blue; } }
  &__link {
    &:not(:last-child) {
      margin-right: 22px; } } }

.map-marker {
  position: absolute;
  text-align: center;
  font-size: 0;
  backface-visibility: hidden;
  cursor: pointer;
  @include hover {
    .map-marker__icon .icon {
      transform: scale(1.3); } }
  .section__map-wrap.is-animate & {
    $transition-icon-delay: 0.75s;
    $transition-text-delay: 0.15s;
    @for $i from 1 through 16 {
      &:nth-child(#{$i}) {
        .map-marker__icon {
          opacity: 1;
          transform: translateY(0);
          transition-delay: $transition-icon-delay;
          $transition-text-delay: $transition-icon-delay + 0.12s;
          $transition-icon-delay: $transition-icon-delay + random(5) * 0.09s; }
        .map-marker__title {
          opacity: 1;
          transform: translateX(0);
          transition-delay: $transition-text-delay; } } } }
  &__icon {
    position: relative;
    margin-bottom: 4px;
    transition: transform .25s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity .3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transform: translateY(-20px);
    opacity: 0;
    .icon {
      position: relative;
      font-size: 21px;
      transition: transform .45s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      .map-marker.is-active & {
        transform: scale(1.3); } } }
  &__title {
    position: relative;
    font-size: 16px;
    font-weight: 900;
    line-height: 1;
    opacity: 0;
    letter-spacing: -0.006em;
    transition: opacity .45s ease-out, transform .45s ease-out;
    transform: translateX(-15px);
    color: $textLight; } }

.map-point {
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: $grey;
  opacity: 0;
  transition: opacity .58s ease-in-out, transform .58s ease-in-out;
  transform: translateY(-20px);
  .section__map-wrap.is-animate & {
    $transition-delay: 0s;
    opacity: 1;
    transform: translateY(0);
    @for $i from 1 through 16 {
      &:nth-child(#{$i}) {
        transition-delay: $transition-delay;
        $transition-delay: $transition-delay + random(2) * 0.09s; } } }
