.hero {
  @include cover;
  &__outer {
    height: 100%;
    padding-top: 200px;
    padding-bottom: 50px;
    @include xxl {
      min-height: 100vh;
      padding-top: 125px;
      padding-bottom: 30px; } }
  &__content {
    // width: 50%
    width: 100%;
    max-width: 854px;
    @include xxl {
      max-width: 754px; } }
  &__logo {
    display: block;
    margin-bottom: 100px;
    @include xxl {
      margin-bottom: 50px; } }
  &__title {
    color: $textBlack;
    margin-right: -10px;
    margin-bottom: 43px; }
  &__descr {
    margin-bottom: 87px;
    line-height: 1.555;
    letter-spacing: -0.1px;
    color: $textLight;
    @include xxl {
      margin-bottom: 70px; } }
  &__actions {
    display: flex;
    align-items: center; }
  &__question-btn {
    margin-right: 93px; } }
